:root, :host {
    /**
     * Width of the separator between the locked rows and the body
     */
    --b-locked-rows-separator-width : 1px;

    /**
     * Color of the separator between the locked rows and the body. Matches the splitter color by default.
     */
    --b-locked-rows-separator-color : var(--b-grid-splitter-color, var(--b-grid-splitter-narrow-color));
}

.b-locked-rows {
    z-index : 1;

    /* Scrollbar hidden when no *data* rows. (Header is counted as a row) */
    [aria-rowcount="1"] {
        .b-virtual-scrollers {
            flex-basis : 0;
        }
    }

    /* When empty, allow the header's box-shadow to escape the bounds so that it looks right */
    &:has(.b-widget-scroller.b-grid-empty) {
        overflow : visible;
        .b-grid-body-wrap, .b-grid-panel-body {
            overflow : visible;
        }
    }

    /* When *not* empty, the top grid has a similar box-shadow to the header */
    &:has(.b-grid-row) {
        box-shadow    : var(--b-grid-header-box-shadow);
        border-bottom : var(--b-locked-rows-separator-width) solid var(--b-locked-rows-separator-color);
    }

    /* We must not see the "No records to display" message. */
    .b-widget-scroller.b-grid-empty {
        min-height : 0;
    }

    .b-grid-row.b-last {
        --b-grid-row-border-width : 0;
    }
}
