:root, :host {
    /**
     * Row expander body's border bottom width
     */
    --b-row-expander-border-bottom-width : var(--b-grid-row-border-width);

    /**
     * Row expander body's font weight
     */
    --b-row-expander-font-weight         : var(--b-grid-cell-font-weight);

    /**
     * Row expander body's background color
     */
    /* --b-row-expander-background       : null; */

    /**
     * Row expander body's padding
     */
    /* --b-row-expander-padding          : null; */

    /**
     * Row expander body's border bottom color
     */
    --b-row-expander-border-bottom-color : var(--b-grid-row-border-color);

    /**
     * Row expander body's color
     */
    --b-row-expander-color               : var(--b-grid-cell-color);
}

.b-row-expander-body {
    display      : flex;
    inset-inline : 0;

    background   : var(--b-row-expander-background);
    color        : var(--b-row-expander-color);
    font-weight  : var(--b-row-expander-font-weight);
    padding      : var(--b-row-expander-padding, 0);

    &, &.b-no-resize-observer.b-resize-monitored {
        position : absolute;
    }
}

/* Applies when the rowExpander has spanRegions set to true */
.b-grid-vertical-scroller > .b-row-expander-body {
    z-index    : 100;
    transition : height .3s, top .3s;
    overflow   : hidden;
}

.b-row-expander-loading {
    justify-content : center;
    align-items     : center;
    font-size       : 1.2em;
    display         : flex;
    width           : 100%;

    .b-icon-spinner {
        margin-inline-end : .5em;
    }
}

.b-grid-base .b-grid-row [data-column="expanderActionColumn"] .b-icon {
    transition : rotate 0.15s ease-in-out;
    font-size  : 1em;
    rotate     : 180deg;
}

.b-row-expander-disabled [data-column="expanderActionColumn"] {
    opacity        : 0.2;
    pointer-events : none;
}

.b-row-expander.b-grid-base.b-animating.b-row-expander-animating {

    &.b-auto-height {
        /* When autoHeight, resize of these 2 elements clips collapsing animation when using nested grids */
        .b-grid-body-container, .b-grid-vertical-scroller {
            transition : height .3s; /* Animate these as well */
            overflow-y : hidden !important; /* Scrollbar will appear and disappear otherwise */
        }

        /* Scrollbar header pad will appear and disappear otherwise */
        .b-yscroll-pad {
            display : none;
        }
    }

    .b-grid-row {
        transition : height .3s, top .3s;
    }
}

.b-grid-base .b-row-expander-row-expanded:not(.b-row-is-collapsing) [data-column="expanderActionColumn"] {
    .b-icon-collapse-left {
        rotate : 270deg;
    }

    .b-icon-collapse-right {
        rotate : 90deg;
    }
}

.b-row-expander-row-expanded {
    border-bottom : var(--b-row-expander-border-bottom-width) solid var(--b-row-expander-border-bottom-color);
}
