:root, :host {
    /**
     * Drop indicator size
     */
    --b-row-reorder-indicator-size          : 2px;

    /**
     * Grip icon (font icon)
     */
    --b-row-reorder-grip-icon               : "\f58e";

    /**
     * Drop indicator color
     */
    --b-row-reorder-indicator-color         : var(--b-secondary);

    /**
     * Drop indicator color when invalid
     */
    --b-row-reorder-indicator-invalid-color : var(--b-color-red);

    /**
     * Drag proxy box shadow
     */
    --b-row-reorder-box-shadow              : var(--b-elevation-1);

    /**
     * Drag proxy background when invalid
     */
    --b-row-reorder-invalid-background      : color-mix(in oklab, var(--b-row-reorder-indicator-invalid-color), transparent 80%);

    /**
     * Opacity of the drag proxy (rows being dragged)
     */
    --b-row-reorder-proxy-opacity           : 0.5;
}

/* Hidden by default */
.b-row-drop-indicator {
    display        : none;
    pointer-events : none;
}

.b-row-reordering {
    /* A full-width line. It's translated to the row top, and it's top position makes */
    /* it position correctly between rows. */
    .b-row-drop-indicator {
        position         : absolute;
        display          : block;
        left             : 0;
        width            : 100%;
        z-index          : 2000;

        top              : calc(var(--b-row-reorder-indicator-size) / -2);
        height           : var(--b-row-reorder-indicator-size);
        background-color : var(--b-row-reorder-indicator-color);

        &.b-drag-invalid {
            --b-row-reorder-indicator-color : var(--b-row-reorder-indicator-invalid-color);
        }
    }

    /* Must move above the .b-grid-splitter element during drag */
    .b-grid-body-container {
        z-index : 4;
    }

    .b-drag-original {
        opacity : 0.3;
    }

    .b-grid-group-collapsed.b-row-reordering-target {
        --b-grid-cell-background : var(--b-grid-cell-hover-background);
    }

    .b-row-reordering-target {
        z-index    : 9999;

        box-shadow : 0 1px 0 0 var(--b-row-reorder-indicator-color) inset, 0 -1px 0 0 var(--b-row-reorder-indicator-color) inset;
    }
}

.b-row-reorder-grip {
    /* The column now must carry two child nodes: The gripper and the real content. */
    /* Use grid to arrange these. */
    display               : grid;
    grid-template-columns : auto 1fr;

    &.b-grid-cell-align-end {
        justify-items : end;

        &:before {
            margin-inline-end : auto;
        }
    }

    &.b-grid-cell-align-right {
        justify-items : right;
    }

    &.b-grid-cell-align-center {
        justify-items : center;
    }

    &::before {
        display             : inline-flex;
        align-self          : stretch;
        align-items         : center;
        cursor              : move;
        font-size           : 0.8em;

        content             : var(--b-row-reorder-grip-icon);
        font-family         : var(--b-widget-icon-font-family);
        /* Pad to have a larger hit area, compensate with margin to no occupy more space*/
        padding-inline      : var(--b-grid-cell-padding-inline);
        margin-inline-start : calc(var(--b-grid-cell-padding-inline) * -1);
    }
}

.b-show-grip-on-hover {
    &.b-row-reordering,
    .b-grid-row:not(:hover, .b-hover) {
        .b-row-reorder-grip::before {
            visibility : hidden;
        }
    }
}
.b-row-reorder-proxy {
    display        : flex;
    flex-direction : column;
    width          : auto !important;
    height         : auto !important;
    z-index        : 10000;
    overflow       : visible;

    .b-grid-row {
        position : relative;
    }

    &.b-drag-copy {
        .b-row-proxy-copy {
            display : flex;
        }
    }

    .b-row-proxy-copy {
        display            : none;
        height             : 1em;
        width              : 1em;
        align-items        : center;
        justify-content    : center;
        position           : absolute;
        inset-inline-start : -.5em;
        top                : -.5em;
        border-radius      : 50%;
        padding            : .75em;
        z-index            : 20000;

        background         : var(--b-panel-background);
        box-shadow         : var(--b-row-reorder-box-shadow);
    }

    .b-grid-row {
        width    : 100%;
        position : relative;
    }

    &.b-dragging {
        transition : margin-top 0.2s, opacity 0.2s;
        background : transparent;
        opacity    : var(--b-row-reorder-proxy-opacity);

        &.b-drag-invalid {
            --b-grid-cell-background : var(--b-row-reorder-invalid-background);
        }

        .b-grid-row {
            transition : transform 0.2s, background-color 0.2s;
        }
    }
}

.b-row-drag-count-indicator:before {
    inset-inline-end : -.7em;
}

/* Prevent showing drag handle in tree-grouped parent rows */
.b-grid-tree-grouped .b-tree-parent-row .b-row-reorder-grip::before {
    content : none;
}
