.b-row-editor {
    .b-bottom-toolbar {
        .b-toolbar-content {
            justify-content : flex-end;
        }

        /* .b-editor-instant-update & { */
        /*     display : none; */
        /* } */
    }

    /* Hide Save button in read only editor */
    &.b-read-only {
        [data-ref="save"] {
            display : none;
        }
    }

    :is(.b-panel-overlay-right,.b-panel-overlay-left) {
        .b-toolbar-content > * {
            flex      : 1 1 33%;
            max-width : 10em;
        }
    }
}
