:root, :host {
    /**
     * Size of the fill handle
     */
    --b-fill-handle-handle-size : .8em;
}

.b-fill-handle-handle {
    translate           : -50% -50%;
    position            : absolute;
    cursor              : crosshair;
    z-index             : 200;
    background-clip     : content-box;
    user-select         : none;
    -webkit-user-select : none;

    background          : var(--b-grid-cell-focused-outline-color);
    border              : 1px solid var(--b-grid-cell-background);
    width               : var(--b-fill-handle-handle-size);
    height              : var(--b-fill-handle-handle-size);
}

/* Used when the fill handle is drawn where there is not enough width for it */
.b-fill-handle-handle-right-edge {
    border-right : 0;
    translate    : -100% -50%;
    width        : 0.5em; /* Slightly larger than half the original size */
}

.b-fill-selection-border {
    position            : absolute;
    border-radius       : .1em;
    pointer-events      : none;
    z-index             : 2;
    user-select         : none;
    -webkit-user-select : none;

    border              : var(--b-grid-cell-focused-outline-width) solid var(--b-grid-cell-focused-outline-color);
}

.b-indicate-crop {
    opacity : 0.4;
}

.b-rtl .b-fill-handle-handle {
    translate : 50% -50%;

    &.b-fill-handle-handle-left-edge {
        border-right : 0;
        translate    : 100% -50%;
        width        : 0.5em; /* Slightly larger than half the original size */
    }
}
