/* @after Core/widget/ChipView.css */

:root, :host {
    /**
     * Group bar separator icon (font icon)
     */
    --b-group-bar-separator-icon  : '\f054';

    /**
     * Group bar separator color
     */
    --b-group-bar-separator-color : var(--b-neutral-50);
}

.b-group-bar {
    display                          : flex;
    flex-wrap                        : nowrap;
    flex                             : 1;
    align-self                       : center;
    gap                              : 2.2em;
    min-height                       : 2em;

    --b-chip-view-chip-padding       : 0.5em 1em;
    --b-chip-view-chip-gap           : 1em;
    --b-chip-view-chip-border-radius : var(--b-button-border-radius);

    .b-chip {
        position : relative;
        overflow : visible;

        &:not(:last-child):after {
            position         : absolute;
            inset-inline-end : -1.5em;

            color            : var(--b-group-bar-separator-color);
            content          : var(--b-group-bar-separator-icon);
            font-family      : var(--b-widget-icon-font-family);
        }

        .b-rtl &:not(:last-child):after {
            scale : -1 0;
        }
    }

    .b-drop-target {
        opacity : .5;

        .b-chip-content {
            visibility : hidden;
        }
    }
}

.b-grid-reordering-columns-with-group-bar.b-grid-header.b-drag-proxy {
    min-width      : 10em !important;
    padding-inline : 1em;

    border-radius  : var(--b-button-border-radius);

    .b-grid-header-text {
        padding : 0.5em 0;
    }

    &, & .b-grid-header-text-content {
        width : auto !important;
    }
}

.b-grid-base.b-dragging-header:not(.b-tree-group) .b-group-bar {
    .b-drop-target {
        display : none;
    }

    .b-chip::after {
        content : none;
    }

    &:hover {
        .b-chip {
            display : none;
        }

        .b-drop-target {
            display : flex;
        }
    }
}

