:root, :host {
    /**
     * Font size for summary labels
     */
    --b-grid-summary-label-font-size : 1em;

    /**
     * Font size for summary values
     */
    --b-grid-summary-value-font-size : 1em;
}

.b-summary-wrap {
    display               : grid;
    column-gap            : 1em;
    grid-template-columns : min-content auto;
    align-items           : center;
    width                 : 100%;

    .b-grid-summary-label {
        font-size : var(--b-grid-summary-label-font-size);
    }

    .b-grid-summary-value {
        overflow      : clip;
        text-overflow : ellipsis;

        font-size     : var(--b-grid-summary-value-font-size);

        /* Spans two columns if there's no label */
        &.b-no-label {
            grid-column : span 2;
        }
    }
}
