/* @after Core/widget/Panel.css */

:root, :host {
    /**
     * Transition duration for column operations such as showing, hiding and collapsing
     */
    --b-grid-column-transition-duration : var(--b-default-transition-duration);

    /**
     * Transition duration for row operations such as adding and removing
     */
    --b-grid-row-transition-duration    : var(--b-default-transition-duration);

    /**
     * Padding for the message shown when the grid is empty
     */
    --b-grid-empty-padding              : var(--b-widget-padding);

    /**
     * Text color for the message shown when the grid is empty
     */
    --b-grid-empty-color                : var(--b-text-2);

    /**
     * Padding for the grid panel header (when having a title or panel tools)
     */
    --b-grid-panel-header-padding       : var(--b-panel-with-header-padding);

    /**
     * Grid background color
     */
    --b-grid-background                 : var(--b-neutral-100);

    /**
     * Grid cell border width. By default also applies to headers
     */
    --b-grid-cell-border-width          : 1px;

    /**
     * Grid cell focused outline width
     * @state Focused
     */
    --b-grid-cell-focused-outline-width : var(--b-widget-focus-outline-width);

    /**
     * Grid cell focused outline color
     * @state Focused
     */
    --b-grid-cell-focused-outline-color : var(--b-widget-focus-outline-color);

    /**
     * Grid cell inner gap
     */
    --b-grid-cell-gap                   : 0.5em;

    /**
     * Grid cell padding block. Defaults to 0, since cells are sized programmatically and content is centered vertically
     */
    --b-grid-cell-padding-block         : 0;

    /**
     * Grid cell padding inline. By default also applies to headers
     */
    --b-grid-cell-padding-inline        : var(--b-widget-padding);

    /**
     * Grid cell font size
     */
    --b-grid-cell-font-size             : 1em;

    /**
     * Grid cell font weight
     */
    --b-grid-cell-font-weight           : var(--b-widget-font-weight);

    /**
     * Grid's default row height, if not set programmatically or through data
     */
    --b-grid-row-height                 : 45px;

    /**
     * Grid row border width (bottom border)
     */
    --b-grid-row-border-width           : 1px;

    /**
     * Grid row z-index
     */
    --b-grid-row-zindex                 : 1;

    /**
     * Grid splitter width, when not using the RegionResize feature
     */
    --b-grid-splitter-width             : 1px;

    /**
     * Grid cell border color
     */
    --b-grid-cell-border-color          : var(--b-border-7);

    /**
     * Grid cell background
     */
    --b-grid-cell-background            : var(--b-neutral-100);

    /**
     * Grid cell color
     */
    --b-grid-cell-color                 : var(--b-widget-color);

    /**
     * Grid cell color when selected & hovered
     * @state Selected
     */
    --b-grid-cell-selected-color        : var(--b-widget-color);

    /**
     * Grid row border color (defaults to use cell border color)
     */
    --b-grid-row-border-color           : var(--b-grid-cell-border-color);

    /**
     * Grid cell dirty indicator color
     */
    --b-grid-cell-dirty-color           : var(--b-color-red);

    /**
     * Grid splitter color, when not using the RegionResize feature
     */
    --b-grid-splitter-narrow-color      : var(--b-neutral-80);

    /* Internal var */
    --bi-grid-cell-overflow             : clip;

    /**
     * Row "Skeleton" background color when lazy loading
     */
    --b-grid-row-placeholder-color      : var(--b-neutral-95);
}

