.b-grid-base {
    overflow                            : clip;
    min-width                           : 0; /* For flex to apply as it should, when in a flex container */

    --b-panel-bottom-toolbar-padding    : var(--b-grid-header-padding);
    --b-panel-header-padding            : var(--b-grid-panel-header-padding);
    --b-panel-background                : var(--b-grid-background);
    --b-panel-top-toolbar-margin-inline : 0;
    --b-panel-top-toolbar-border-radius : 0;

    &:where(.b-outer:not(.b-auto-height)) {
        height : 100%;
    }

    &.b-auto-height {
        height : auto;
        flex   : none;

        .b-grid-vertical-scroller {
            position : relative;
        }

        &.b-grid-empty .b-grid-body-container {
            height : unset !important;
            flex   : 1;
        }
    }

    .b-empty-text {
        display : none;
    }

    &.b-grid-empty {
        .b-empty-text {
            display : block;
            position: relative;
            color   : var(--b-grid-empty-color);
            padding : var(--b-grid-empty-padding);
            z-index : 20;
        }
    }

    /* Applies to both row and cell */
    .b-editing {
        overflow : visible;
        contain  : unset;
        z-index  : 1;
    }

    &.b-splicing-rows-toggle .b-grid-row:is(.b-adding, .b-removing, .b-repositioning),
    &.b-splicing-rows-remove .b-grid-row:is(.b-removing, .b-repositioning),
    &.b-splicing-rows-insert .b-grid-row:is(.b-adding, .b-repositioning) {
        transition     : top var(--b-grid-row-transition-duration), left var(--b-grid-row-transition-duration);
        z-index        : calc(var(--b-grid-row-zindex) - 1);
        /* Should not trigger and mouse events on a row being removed */
        pointer-events : none;
    }

    &.b-collapsing-column {
        .b-collapsing,
        .b-collapsing .b-grid-header:not(:first-child) {
            min-width : 0 !important;
            overflow  : clip !important;
            padding   : 0;
        }

        .b-collapsing .b-grid-header:not(:first-child) {
            width : 0 !important;
        }
    }

    /* This class gets added and then removed, to let cells transition to their normal settings */
    .b-expanding-start .b-grid-header:not(:first-child) {
        min-width : 0 !important;
        width     : 0 !important;
        padding   : 0;
    }

    /* This class is kept during the expand */
    &.b-expanding-column {
        .b-expanding .b-grid-header:not(:first-child) {
            min-width : 0 !important;
        }
    }

    &.b-collapsing-column,
    &.b-expanding-column,
    &.b-showing-column,
    &.b-hiding-column,
    &.b-toggling-columns {
        .b-grid-header {
            transition-property : min-width, max-width, width, flex, padding;
            transition-duration : var(--b-grid-column-transition-duration);
        }

        /* Cell widths automatically matches header widths, no need to transition */
        .b-grid-cell {
            transition-property : padding;
            transition-duration : var(--b-grid-column-transition-duration);
        }
    }

    /* Prevent transitions when datasets are refreshed (sort, load, filter etc). Also allow our own widgets to use */
    /* transitions, such as a widget column with a SlideToggle. */
    /* THIS RULE IS TOO EXPENSIVE, MOVING TO CELL */
    /* &:not(.b-animating, :has(.b-widget-cell.b-focused)) .b-grid-row:not(.b-grid-row-updating) .b-grid-cell * { */
    /*     &, */
    /*     &::before, */
    /*     &::after { */
    /*         transition : none; */
    /*     } */
    /* } */

    .b-grid-header:is(.b-showing, .b-hiding) {
        min-width : 0 !important;
        padding   : 0 !important;

        &.b-flex {
            flex : 0 !important;
        }
    }

    .b-toggling-all-expand {
        &.b-grid-header {
            width : 0 !important;
        }
    }
}

/*
 * Implements the fillLastColumn config. It causes the last leaf header, and the last cell in each row to flex-grow to
 * consume all available space when there are no configured flexed columns and rows are not wider than their container.
 */
.b-fill-last-column {
    .b-grid-footers:not(.b-has-flex, .b-horizontal-overflow) .b-grid-footer:last-child,
    .b-grid-headers:not(.b-has-flex, .b-horizontal-overflow) .b-last-parent,
    .b-grid-headers:not(.b-has-flex, .b-horizontal-overflow) .b-last-leaf:not(.b-drop-placeholder) {
        flex-grow               : 1;
        border-inline-end-color : transparent;
    }

    .b-grid-headers:not(.b-has-flex, .b-horizontal-overflow) .b-last-parent > .b-grid-header-text {
        border-inline-end-color : transparent;
    }

    .b-grid-cell:last-child,
    .b-row-expander-row-expanded .b-grid-cell:nth-last-child(2) {
        --b-grid-cell-border-width : 0;
    }
}

.b-grid-panel-body {
    display        : flex;
    flex-direction : column;
    flex           : 1;
    position       : relative;
    overflow       : hidden;

    /* If we're not autoHeighting the grid, the scroller element must always be full height so that */
    /* mouse wheel events are received from below the row content. */
    &:not(.b-auto-height) {
        > .b-grid-body-container {
            /* Auto height does not work in edge if these styles are applied */
            flex    : 1 1 0;
            contain : strict;

            > .b-grid-vertical-scroller {
                min-height : 100%;
            }
        }
    }
}

.b-grid-base {
    /* Sticky content must be visible outside of its container. */
    &.b-enable-sticky {
        .b-sticky-cell,
        .b-grid-sub-grid,
        .b-grid-vertical-scroller,
        .b-grid-row {
            overflow : visible !important;
        }
    }

    > .b-panel-body-wrap {
        /* Also let it have a different color than other toolbars*/
        --b-toolbar-background : var(--b-grid-toolbar-background);

        > .b-top-toolbar {
            transition    : background-color var(--b-default-transition-duration), border var(--b-default-transition-duration);

            /* If grid headers use a border, so should the top toolbar to distinguish it from them */
            border-bottom : var(--b-grid-header-container-border-width) solid var(--b-grid-header-border-color);
        }
    }

    &:not(.b-auto-height) > .b-panel-body-wrap > .b-bottom-toolbar {
        /* If grid headers use a border, so should the bottom toolbar */
        /* (not in an auto height grid, bottom rows border is enough) */
        border-top : var(--b-grid-header-container-border-width) solid var(--b-grid-header-border-color);
    }
}

.b-grid-body-container {
    position : relative;
}

.b-grid-vertical-scroller {
    display  : flex;
    overflow : clip;
}

/* Icons inside a grid move the icon font to the pseudo, to preserve backwards compat styling a bit better */
.b-grid-base .fa {
    font-family : inherit;

    &::before {
        font-family : var(--b-widget-icon-font-family)
    }
}

.b-no-transitions {
    .b-grid-row {
        transition : none !important;
    }
}
