/* Summary formatting defined in mixin/SummaryFormatter.css */

:root, :host {
    /**
     * Background for group summary cells
     */
    --b-group-summary-background : var(--b-neutral-99);
}

.b-grid-row.b-group-footer {
    --b-grid-cell-border-color : transparent;
    --b-grid-cell-background   : var(--b-group-summary-background);

    td {
        padding : .25em 0;
    }

    .b-grid-summary-label {
        padding-inline-end : 1em;
    }

    .b-grid-summary-value {
        width : 100%;
    }
}

/* No overflow for title in collapsed group header which is also showing summaries. */
.b-grid-group-collapsed.b-header-summary .b-group-title {
    --bi-grid-cell-overflow : clip;
}
