/* @after Grid/view/SubGrid.css */

:root, :host {
    /**
     * Percent bar border radius
     * @rendition Bar
     */
    --b-percent-column-bar-border-radius      : var(--b-widget-border-radius);

    /**
     * Percent bar max height
     * @rendition Bar
     */
    --b-percent-column-bar-max-height         : 0.6em;

    /**
     * Percent bar border
     * @rendition Bar
     */
    --b-percent-column-bar-border             : none;

    /**
     * Thickness of the outer circle in "circle" mode
     * @rendition Circle
     */
    --b-percent-column-circle-thickness       : 0.5em;

    /**
     * Label font size in "circle" mode
     * @rendition Circle
     */
    --b-percent-column-circle-label-font-size : .8em;

    /**
     * Percent bar background color (the "empty" part of the bar)
     * @rendition Bar
     */
    --b-percent-column-bar-background         : var(--b-neutral-95);

    /**
     * Fill color for the bar when value is below `lowThreshold`
     * @rendition Bar
     */
    --b-percent-column-low-fill-background    : var(--b-color-orange);

    /**
     * Value label's color when the bar is wide, and label gets moved inside the bar
     * @rendition Bar
     */
    --b-percent-column-wide-value-color       : var(--b-neutral-100);

    /**
     * Value label's color when the value is below `lowThreshold`
     * @rendition Bar
     */
    --b-percent-column-low-value-color        : var(--b-secondary);

    /**
     * Value label's color when the value is zero
     * @rendition Bar
     */
    --b-percent-column-zero-value-color       : transparent;

    /**
     * Background color for the circle in "circle" mode
     * @rendition Circle
     */
    --b-percent-column-circle-background      : var(--b-grid-cell-border-color);

    /**
     * Value label's color in "circle" mode
     * @rendition Circle
     */
    --b-percent-column-circle-label-color     : var(--b-grid-cell-color);

    /* Internal var */
    --bi-percent-column-transition            : none;
}

