/* Overflow must be visible for sticky merged cells to work */
.b-merge-cells {
    /* FF specific override */
    /* .b-grid-base.b-firefox &, */
    /* &.b-grid-base.b-firefox { */
    /*     .b-single-child .b-grid-sub-grid:not(.b-time-axis-sub-grid):not(.b-overlay-scrollbar) { */
    /*         overflow : visible !important; */
    /*     } */
    /* } */

    .b-single-child .b-grid-sub-grid:not(.b-grid-sub-grid-collapsed, .b-time-axis-sub-grid, .b-horizontal-overflow) {
        overflow : clip !important;
    }

    .b-grid-row:not(.b-selected, .b-hover) .b-merged-cell {
        border-bottom : none;
    }

    .b-grid-row:is(.b-selected, .b-hover) {
        &:has(.b-merged-cell) {
            overflow : visible;
        }

        .b-merged-cell {
            top        : -1px;
            height     : calc(100% + 1px);
            border-top : var(--b-grid-row-border-width) solid var(--b-grid-row-border-color);
        }
    }
}

/* Merged cells float above rows */
.b-grid-merged-cells {
    position                  : absolute;
    contain                   : strict;
    z-index                   : 2; /* Above row */
    display                   : flex;
    align-items               : flex-start;

    border-bottom             : var(--b-grid-cell-border-width) solid var(--b-grid-cell-border-color);

    --b-grid-cell-background  : transparent;
    --b-grid-row-border-color : transparent;
    --bi-grid-cell-overflow   : visible;

    .b-merge-cells-passthrough & {
        pointer-events : none;
    }

    /* Stick the single cell inside a merged range */
    .b-grid-cell {
        position : sticky;
        top      : 0;
        flex     : 1;
        contain  : none;
    }

    &.b-selected {
        background : var(--b-grid-cell-selected-background);
    }
}
