html.b-print-root,
.b-print-root body {
    margin   : 0;
    height   : auto;
    overflow : unset;
    display  : block;
}

.b-print-root {
    .b-float-root,
    .b-scrollbar-measure-element,
    .b-grid-header-resize-handle {
        display : none !important;
    }
}

.b-print:not(.b-safari) .b-page-wrap {
    page-break-after : always;
}
