.b-row-resize {
    .b-grid-row {
        &.b-resize-handle {
            cursor : row-resize !important;
        }
        &.b-resizing {
            z-index : 2;
        }
    }

    &.b-row-resizing {
        .b-grid-row {
            pointer-events: none;
        }

        * {
            cursor : row-resize !important;
        }
    }
}
