.b-split-container {
    display  : flex;
    /* width   : -webkit-fill-available; */
    /* width   : -moz-available; */
    /* width   : stretch; */
    /* width    : 100%; */
    height   : 100%;
    position : relative;
    overflow : hidden;

    &.b-split-horizontal,
    &.b-split-both {
        flex-direction : column;
    }

    &.b-split-vertical {
        flex-direction : row;
    }

    .b-split-top,
    .b-split-bottom {
        display : flex;
        flex    : 1;
    }

    > .b-grid-base,
    > div > .b-grid-base {
        flex       : 1;
        /* Override flex-basis from .b-outer to make each split same size */
        flex-basis : 0;
        /* Also override any configured height */
        height     : unset !important;
    }
}
